.form {
  display: flex;
  flex-direction: column;
  padding: 10px;

  @media screen and (max-width: 320px) {
    width: 100%;
  }
  @media screen and (min-width: 321px) {
    width: 320px;
  }
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    align-items: center;
  }
}

.title_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
}
.wrapper {
  display: flex;
  flex-direction: row;
}

.link {
  position: relative;
  display: inline-block;
  padding: 0 0 8px 0;
  text-decoration: none;
  color: var(--accent-color);

  &:hover::after,
  &:focus::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 1.5px;
    background-color: var(--accent-color);
    border-radius: 2px;
  }
}

.iconInput_wrap {
  position: relative;
  &:hover label {
    color: var(--accent-color);
  }
  &:hover input {
    outline: 1.5px solid var(--accent-color);
  }
}

.icon_wrap {
  position: absolute;
  width: 40px;
  height: 30px;
  top: 43%;
  right: 15px;
  padding: 0;
  background-color: #ffffff;
  border: none;
  outline: none;
}
